import { applyMiddleware, compose, createStore } from 'redux';
import createSagaMiddleware from 'redux-saga';
import { createProvider } from 'react-redux';

import reducer from './reducer';
import saga from './saga';
import { PREFIX } from './constants';

const getDevTools = () => {
        return typeof window !== 'undefined' && window.devToolsExtension ? window.devToolsExtension({
            name: `${PREFIX}-store`
        }) : f => f;
};

const sagaMiddleware = createSagaMiddleware();
const createStoreWithSaga = compose(
    applyMiddleware(sagaMiddleware),
    getDevTools()
)(createStore);

export const storiesStore = createStoreWithSaga(reducer);
sagaMiddleware.run(saga);

export const StoriesProvider = createProvider(PREFIX);



